*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

* {
  border: 0;
  margin: 0;
  padding: 0;
}

*, :before, :after {
  box-sizing: border-box;
}

:focus, :active, a:focus, a:active {
  ouline: none;
}

nav, footer, header, aside {
  display: block;
}

html, body {
  height: 100%;
  width: 100%;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  font-size: 14px;
  line-height: 1;
}

input, button, textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  border: 0;
  padding: 0;
}

a, a:visited, a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: 400;
}

:root {
  --current-color: currentColor;
  --main-color: #01996d;
  --main-background-color: #fff;
  --second-background-color: #245852;
  --main-text-color: #424242;
  --second-text-color: #000;
  --third-text-color: #fff;
  --navigation-text-color: #292929;
  --button-text-color: #fff;
  --hero-border-color: #d6d6d6;
  --hero-background-color: #faf7ff;
  --hero-text-color: #484848;
  --story-background-color: #245852;
  --story-text-color: #fff;
  --button-border: 1px;
  --hero-border: 1px;
  --double-border: 2px;
  --bold-border: 4px;
  --button-border-style: solid;
  --hero-border-style: solid;
  --button-border-radius: 4px;
  --main-font: "Lato", sans-serif;
  --header-font: "Georgia", sans-serif;
  --header-text-size: 20px;
}

.wrapper {
  align-items: center;
  margin: 0 auto;
  display: flex;
}

body.modal-open, body.menu-open {
  overflow: hidden;
}

.backdrop {
  z-index: 1500;
  width: 100%;
  height: 100%;
  background-color: rgba(193, 193, 193, .5);
  padding: 30px;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: auto;
}

.modal {
  width: 80%;
  height: 70%;
  background-color: #000;
  position: absolute;
  top: 10px;
  left: 10px;
  overflow: hidden;
}

.backdrop.is-hidden .modal {
  transform: translateX(100%);
}

.modal__close-button {
  z-index: 1;
  position: absolute;
  top: 15px;
  right: 15px;
}

.close-button {
  width: 140px;
  height: 140px;
  color: red;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  outline: none;
}

.close-button:hover, .close-button:focus {
  color: orange;
  background-color: gray;
}

